import { isNumber } from '../../utils';

export default {
	name: 'ClBadge',

	props: {
		value: [String, Number],
		max: Number,
		isDot: Boolean,
		hidden: Boolean,
		type: String
	},

	computed: {
		content() {
			if (this.isDot) return '';

			const value = this.value;
			const max = this.max;

			if (isNumber(value) && isNumber(max) && max > 0) {
				return max < value ? `${max}+` : value;
			}

			return value;
		}
	}
};
